.i {
    display: flex;
    height: 100vh;
    background-color: #F6F6DB;
  }
  
  .i-desc {
    font-size: 20px;
  }
  .i-left {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .i-right {
    flex: 1;
    position: relative;
  }
  
  .i-left-wrapper {
    padding: 30px;
    height: 50%;
    flex-direction: column;
    justify-content: space-between;
  }
  
  .i-intro {
    color: #59b256;
    font-size: 50px;
    font-weight: 300;
  }

  
  .i-img {
    margin-top:130px;
    margin-left:60px;
    width: 50%;
    height: 50%;
  }
  
  .i-bg {
    clip-path: polygon(100% 0%, 100% 51%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
    background-color: #59b256;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
  }
  
  
  @media screen and (max-width: 480px) {
    .i {
      flex-direction: column;
    }
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }
    .i-desc{
      display: none;
    }
  }